.schedule {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  height: 100%;
}

.schedule div {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.schedule div.highlighted {
  background-color: white;
}

.schedule div.highlighted {
  border-top: 1px solid #f5f5f5;
}

.schedule span.handel {
  position: relative;
  width: calc(100% + 8px) ;
  height: 2px;
  background-color: red;
  margin: -4px;
  z-index: 100;
  -webkit-transition: all 0.25;
  -o-transition: all 0.25;
  transition: all 0.25;
}

.schedule div span {
  text-align: left;
  padding: 8px;
  font-size: 10px;
  letter-spacing: 0.05em;
  /*color: rgba(0, 0, 0, 0.48);*/
}

.schedule .shadow {
  -webkit-box-shadow: 0px 1px 2px 0px #e3e3e3;
          box-shadow: 0px 1px 2px 0px #e3e3e3;
}



